  .parameter-box-vertical {
    margin-bottom: 16px;  // Adjust the margin as needed
  }
  
  .expanded-content {
    margin-top: 10px;
  }
  
  .parameter-box {
    margin-bottom: 10px;
    padding: 10px;
    border: 1px solid #ddd;
  }
  
  /* historyPage.scss */

  .parameter-box-vertical {
